<template>
  <div id="blogDetail">
    <b-container>
      <b-row>
        <b-col class="d-flex mt-2 flex-column align-items-center justify-content-center">
          <b-card
            :title="title"
            :img-src="coverImageUrl"
            img-top
            header-tag="header"
            footer-tag="footer"
            tag="article"
            class="mb-2 blogEntry"
          >
            <span v-html="content"></span>
            <template v-slot:footer>
              <b-button to="/blog" variant="danger">Back</b-button>
              <b-card-text>Created: {{new Date(when).toLocaleString("en-us")}}</b-card-text>
              <b-card-text>Keywords:</b-card-text>
              <div class="d-flex align-items-center flex-wrap">
                <span class="keyword" v-for="keyword in keywords" :key="keyword">{{keyword}}</span>
              </div>
            </template>
          </b-card>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <div id="disqus_thread"></div>
          <noscript>
            Please enable JavaScript to view the
            <a
              href="https://disqus.com/?ref_noscript"
            >comments powered by Disqus.</a>
          </noscript>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import url from "../util/url";
export default {
  data: function() {
    return {
      content: "",
      keywords: [],
      title: "",
      when: null,
      coverImageUrl: ""
    };
  },
  created() {
    axios
      .get(url + "/blog/blogEntries/" + this.$route.params.id)
      .then(res => {
        this.title = res.data.blogEntry.title;
        this.content = res.data.blogEntry.content;
        this.coverImageUrl = res.data.blogEntry.coverImageUrl;
        this.keywords = res.data.blogEntry.keywords;
        this.when = res.data.blogEntry.when;
      })
      .catch(err => {
        if (err.response) {
          this.$bvToast.toast(err.response.data.message);
        } else {
          this.$bvToast.toast("An error occured: " + JSON.stringify(err));
        }
      });

    var disqus_config = function() {
      this.page.url = "https://serkanozel.me";
      this.page.identifier = "blogDetail";
    };
    (function() {
      var d = document,
        s = d.createElement("script");
      s.src = "https://serkanozel.disqus.com/embed.js";
      s.setAttribute("data-timestamp", +new Date());
      (d.head || d.body).appendChild(s);
    })();
  }
};
</script>

<style>
#blogDetail {
  width: 90%;
}
img {
  width: 100%;
}
.keyword {
  background-color: lightblue;
  color: black;
  border-radius: 10px;
  border: 1px solid black;
  padding: 0.3rem;
  margin: 0.3rem;
  width: fit-content;
}
.blogEntry {
  box-shadow: 10px 10px 5px grey;
  max-width: 40rem;
  width: 100%;
  border: 2px solid rgb(0, 135, 135);
  padding: 0.2rem;
}
</style>
